import PropsTypes from 'prop-types'
import Button from 'react-bootstrap/Button'

const Btn = ({ btnStyle, text, onClick }) => {
  return (
    <Button
      variati='primary'
      onClick={onClick}
    >
      {text}
    </Button>
  )
}

Btn.defaultProps = {
  btnStyle: 'btn btn-primary',
}

Btn.propsType = {
  text: PropsTypes.string,
  onClick: PropsTypes.func.isRequired,
}

export default Btn
